<template>
    <el-row>
        <el-col :span="24">
            <el-tabs :tab-position="tabPosition" v-model="activeName" @tab-click="handleClick">
                <el-tab-pane name="user" :key="'user'"><span slot="label"><i class="el-icon-date"></i>用户管理</span>
                    <userMan v-if="isuserMan"></userMan>
                </el-tab-pane>
                <el-tab-pane label="配置管理" name="setpl"><i class="el-icon-date"></i>配置管理</el-tab-pane>
                <el-tab-pane label="角色管理" name="role"><i class="el-icon-date"></i>角色管理</el-tab-pane>
                <el-tab-pane label="定时任务补偿" name="target"><i class="el-icon-date"></i>定时任务补偿</el-tab-pane>
            </el-tabs>
        </el-col>
    </el-row>
</template>

<script>
import usermanager from '../usermanager/usermanager'
export default {
    name: "tab",
    data() {
      return {
        activeName : 'user',
        tabPosition : 'left',
        isuserMan: true,
      };
    },
    components: {
      userMan: usermanager,
    //   child2: ytProcess
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
        if (tab.name === 'user') {
          this.isuserMan = true
          //比如点击第一个，只给第一个true 其他页面都是false
        //   this.isChildUpdate2 = false 
        //   this.isChildUpdate3 = false
        //   this.isChildUpdate4 = false
        }
      }
    }
}
</script>

<style>

</style>


